EACE,uBAAA;EACA,gBAAA;AAxHN;AA6HA;;EAEE,aAAA;AA1HF;AA6HA,yBAAA;AACA;EAEI;IACE,aAAA;EA3HJ;EA6HI;IACE,gBAAA;IACA,cAAA;EA3HN;AACF;AAgIA,YAAA;AEthBI;EACE,sBDuBO;ADkYb;AEnZU;;EAIE,sBDaC;ADsYb;AEhZU;EACE,WDSC;ADyYb;AE/YU;EACE,eAAA;EACA,sBDIC;ECHD,WDpBM;ECqBN,YAAA;EACA,cAAA;AFiZZ;AExaI;EACE,yBDuBO;ADoZb;AEraU;;EAIE,yBDaC;ADwZb;AElaU;EACE,cDSC;AD2Zb;AEjaU;EACE,eAAA;EACA,yBDIC;ECHD,WDpBM;ECqBN,YAAA;EACA,cAAA;AFmaZ;AE1bI;EACE,yBDuBO;ADsab;AEvbU;;EAIE,yBDaC;AD0ab;AEpbU;EACE,cDSC;AD6ab;AEnbU;EACE,eAAA;EACA,yBDIC;ECHD,WDpBM;ECqBN,YAAA;EACA,cAAA;AFqbZ;AE5cI;EACE,yBDuBO;ADwbb;AEzcU;;EAIE,yBDaC;AD4bb;AEtcU;EACE,cDSC;AD+bb;AErcU;EACE,eAAA;EACA,yBDIC;ECHD,WDpBM;ECqBN,YAAA;EACA,cAAA;AFucZ;AE9dI;EACE,yBDuBO;AD0cb;AE3dU;;EAIE,yBDaC;AD8cb;AExdU;EACE,cDSC;ADidb;AEvdU;EACE,eAAA;EACA,yBDIC;ECHD,WDpBM;ECqBN,YAAA;EACA,cAAA;AFydZ;AEhfI;EACE,yBDuBO;AD4db;AE7eU;;EAIE,yBDaC;ADgeb;AE1eU;EACE,cDSC;ADmeb;AEzeU;EACE,eAAA;EACA,yBDIC;ECHD,WDpBM;ECqBN,YAAA;EACA,cAAA;AF2eZ","file":"bs-datepicker.scss","sourcesContent":["@import 'utils/scss/variables';\n@import 'utils/scss/mixins';\n\n/* .bs-datepicker */\n.bs-datepicker {\n  display: flex;\n  align-items: stretch;\n  flex-flow: row wrap;\n  background: $main-bg;\n  box-shadow: 0 0 10px 0 $main-box-shadow;\n  position: relative;\n  z-index: 1;\n\n  &:after {\n    clear: both;\n    content: '';\n    display: block;\n  }\n\n  bs-day-picker {\n    float: left;\n  }\n\n  /* button */\n  button:hover,\n  button:focus,\n  button:active,\n  input:hover,\n  input:focus,\n  input:active,\n  &-btns button:hover,\n  &-btns button:focus,\n  &-btns button:active,\n  &-predefined-btns button:active,\n  &-predefined-btns button:focus {\n    outline: none;\n  }\n\n  /* .bs-datepicker-head */\n  &-head {\n    min-width: 270px;\n    height: 50px;\n    padding: 10px;\n    border-radius: 3px 3px 0 0;\n    text-align: justify;\n\n    &:after {\n      content: \"\";\n      display: inline-block;\n      vertical-align: top;\n      width: 100%;\n    }\n\n    /* .bs-datepicker-head button */\n    button {\n      display: inline-block;\n      vertical-align: top;\n      padding: 0;\n      height: 30px;\n      line-height: 30px;\n      border: 0;\n      background: transparent;\n      text-align: center;\n      cursor: pointer;\n      color: $font-color-01;\n      transition: 0.3s;\n\n      &[disabled],\n      &[disabled]:hover,\n      &[disabled]:active {\n        background: $disabled-background;\n        color: $disabled-color;\n        cursor: not-allowed;\n      }\n\n      &.next,\n      &.previous {\n        border-radius: 50%;\n        width: 30px;\n        height: 30px;\n\n        span {\n          font-size: 28px;\n          line-height: 1;\n          display: inline-block;\n          position: relative;\n          height: 100%;\n          width: 100%;\n          border-radius: 50%;\n        }\n      }\n\n      &.current {\n        border-radius: 15px;\n        max-width: 155px;\n        padding: 0 13px;\n      }\n    }\n  }\n\n  &-head {\n    button {\n      &:hover {\n        background-color: rgba(0, 0, 0, 0.1);\n      }\n      &:active {\n        background-color: rgba(0, 0, 0, 0.2);\n      }\n    }\n  }\n\n  /* .bs-datepicker-body */\n  &-body {\n    padding: 10px;\n    border-radius: 0 0 3px 3px;\n    min-height: 232px;\n    min-width: 278px;\n    border: 1px solid $border-color;\n\n    .days.weeks {\n      position: relative;\n      z-index: 1;\n    }\n\n    /* .bs-datepicker-body table */\n    table {\n      width: 100%;\n      border-collapse: separate;\n      border-spacing: 0;\n\n      th {\n        font-size: 13px;\n        color: $font-color-02;\n        font-weight: 400;\n        text-align: center;\n      }\n\n      td {\n        color: $font-color-03;\n        text-align: center;\n        position: relative;\n        padding: 0;\n\n        span {\n          display: block;\n          margin: 0 auto;\n          font-size: 13px;\n          border-radius: 50%;\n          position: relative;\n          /*z-index: 1;*/\n          -moz-user-select: none;\n          -webkit-user-select: none;\n          -ms-user-select: none;\n        }\n\n        // todo: check first :not(.disabled) usage\n        &:not(.disabled):not(.week) span:not(.disabled):not(.is-other-month) {\n          cursor: pointer;\n        }\n\n        &.is-highlighted:not(.disabled):not(.selected) span,\n        span.is-highlighted:not(.disabled):not(.selected) {\n          background-color: $highlighted;\n          transition: 0s;\n        }\n\n        &.is-active-other-month:not(.disabled):not(.selected) span,\n        span.is-active-other-month:not(.disabled):not(.selected) {\n          background-color: $highlighted;\n          transition: 0s;\n          cursor: pointer;\n        }\n\n        span.disabled,\n        &.disabled span {\n          color: $font-color-02;\n        }\n\n        span.selected,\n        &.selected span {\n          color: $font-color-01;\n        }\n\n        span.is-other-month,\n        &.is-other-month span {\n          color: rgba(0, 0, 0, 0.25);\n        }\n\n        &.active {\n          position: relative;\n\n          &.select-start:before {\n            left: 35%;\n          }\n\n          &.select-end:before {\n            left: -85%;\n          }\n        }\n\n        span.active.select-start:after,\n        span.active.select-end:after,\n        &.active.select-start span:after,\n        &.active.select-end span:after {\n          content: \"\";\n          display: block;\n          position: absolute;\n          z-index: -1;\n          width: 100%;\n          height: 100%;\n          transition: 0.3s;\n          top: 0;\n          border-radius: 50%;\n        }\n\n        &:before,\n        span:before {\n          content: \"\";\n          display: block;\n          position: absolute;\n          z-index: -1;\n          top: 6px;\n          bottom: 6px;\n          left: -2px;\n          right: -2px;\n          box-sizing: content-box;\n          background: transparent;\n        }\n\n        &.active.select-start + td.active:before {\n          left: -20%;\n        }\n\n        &:last-child.active:before {\n          border-radius: 0 3px 3px 0;\n          width: 125%;\n          left: -25%;\n        }\n\n        span[class*=\"select-\"],\n        &[class*=\"select-\"] span {\n          border-radius: 50%;\n          color: $font-color-01;\n        }\n      }\n\n      /* .bs-datepicker-body table.days */\n      &.days {\n        td,\n        span {\n          &.active:not(.select-start):before,\n          &.in-range:not(.select-start):before {\n            background: $highlighted;\n          }\n        }\n\n        span {\n          width: 32px;\n          height: 32px;\n          line-height: 32px;\n\n          &.select-start {\n            z-index: 2;\n          }\n          &.is-highlighted.in-range:before,\n          &.in-range.select-end:before {\n            background: none;\n            right: 0;\n            left: 0;\n          }\n        }\n\n        td {\n          &.select-start + td.select-end:before,\n          &.select-start + td.is-highlighted:before,\n          &.active + td.is-highlighted:before,\n          &.active + td.select-end:before,\n          &.in-range + td.is-highlighted:before,\n          &.in-range + td.select-end:before {\n            background: $highlighted;\n            width: 100%;\n          }\n        }\n      }\n\n      /* .bs-datepicker-body table.weeks */\n      &.weeks {\n        tr {\n          td {\n            &:nth-child(2).active:before {\n              border-radius: 3px 0 0 3px;\n              left: 0;\n              width: 100%;\n            }\n          }\n        }\n      }\n\n      &:not(.weeks) {\n        tr {\n          td{\n            &:first-child:before {\n              border-radius: 3px 0 0 3px;\n            }\n          }\n        }\n      }\n\n      &.years {\n        td {\n          span {\n            width: 46px;\n            height: 46px;\n            line-height: 45px;\n            margin: 0 auto;\n          }\n        }\n\n        tr:not(:last-child) {\n          td {\n            span {\n              margin-bottom: 8px;\n            }\n          }\n        }\n      }\n\n      &.months {\n        td {\n          height: 52px;\n\n          span {\n            padding: 6px;\n            border-radius: 15px;\n          }\n        }\n      }\n    }\n  }\n\n  /* .current-timedate */\n  .current-timedate {\n    color: $font-color-03;\n    font-size: 15px;\n    text-align: center;\n    height: 30px;\n    line-height: 30px;\n    border-radius: 20px;\n    border: 1px solid $border-color;\n    margin-bottom: 10px;\n    cursor: pointer;\n    text-transform: uppercase;\n    -moz-user-select: none;\n    -webkit-user-select: none;\n    -ms-user-select: none;\n\n    span:not(:empty):before {\n      content: \"\";\n      width: 15px;\n      height: 16px;\n      display: inline-block;\n      margin-right: 4px;\n      vertical-align: text-bottom;\n      background: url(data:image/png;base64,);\n    }\n  }\n\n  /* .bs-datepicker-multiple */\n  &-multiple {\n    border-radius: 4px 0 0 4px;\n\n    & + & {\n      margin-left: 10px;\n    }\n\n    .bs-datepicker {\n      box-shadow: none;\n      position: relative;\n\n      &:not(:last-child) {\n        padding-right: 10px;\n      }\n\n      & + .bs-datepicker:after {\n        content: \"\";\n        display: block;\n        width: 14px;\n        height: 10px;\n        background: url(data:image/png;base64,);\n        position: absolute;\n        top: 25px;\n        left: -8px;\n      }\n\n      .left {\n        float: left;\n      }\n\n      .right {\n        float: right;\n      }\n    }\n  }\n\n  /* .bs-datepicker-btns */\n  &-container {\n    padding: 15px;\n  }\n\n  .bs-media-container {\n    display: flex;\n    @media(max-width: 768px) {\n      flex-direction: column;\n    }\n  }\n\n  /*.bs-datepicker-custom-range */\n  &-custom-range {\n    padding: 15px;\n    background: $custom-range-bg;\n  }\n\n  /* .bs-datepicker-predefined-btns */\n  &-predefined-btns {\n    button {\n      width: 100%;\n      display: block;\n      height: 30px;\n      background-color: $btn-bg2;\n      border-radius: 4px;\n      color: $font-color-01;\n      border: 0;\n      margin-bottom: 10px;\n      padding: 0 18px;\n      text-align: left;\n      transition: 0.3s;\n\n      &:active,\n      &:hover {\n        background-color: $btn-bg2-hover;\n      }\n    }\n  }\n\n  /* .bs-datepicker-buttons */\n  &-buttons {\n    display: flex;\n    flex-flow: row wrap;\n    justify-content: flex-end;\n    padding-top: 10px;\n    border-top: 1px solid $border-color;\n\n    .btn-default {\n      margin-left: 10px;\n    }\n  }\n}\n\n/* .bs-timepicker */\n.bs-timepicker {\n  &-container {\n    padding: 10px 0;\n  }\n\n  &-label {\n    color: $font-color-03;\n    margin-bottom: 10px;\n  }\n\n  &-controls {\n    display: inline-block;\n    vertical-align: top;\n    margin-right: 10px;\n\n    button {\n      width: 20px;\n      height: 20px;\n      border-radius: 50%;\n      border: 0;\n      background-color: $btn-bg;\n      color: $font-color-03;\n      font-size: 16px;\n      font-weight: 700;\n      vertical-align: middle;\n      line-height: 0;\n      padding: 0;\n      transition: 0.3s;\n\n      &:hover {\n        background-color: $btn-bg-hover;\n      }\n    }\n\n    input {\n      width: 35px;\n      height: 25px;\n      border-radius: 13px;\n      text-align: center;\n      border: 1px solid $border-color;\n    }\n  }\n\n  .switch-time-format {\n    text-transform: uppercase;\n    min-width: 54px;\n    height: 25px;\n    border-radius: 20px;\n    border: 1px solid $border-color;\n    background: $main-bg;\n    color: $font-color-03;\n    font-size: 13px;\n\n    img {\n      vertical-align: initial;\n      margin-left: 4px;\n    }\n  }\n}\n\nbs-datepicker-container,\nbs-daterangepicker-container {\n  z-index: 1080;\n}\n\n/* screen size < 1024px */\n@media (max-width: 768px) {\n  .bs-datepicker {\n    &-multiple {\n      display: flex;\n\n      & + & {\n        margin-top: 10px;\n        margin-left: 0;\n      }\n    }\n  }\n}\n\n/* theming */\n@each $name, $color in $theme-list {\n  @include theming($name, $color);\n}\n","$main-bg:         #fff !default;\n$custom-range-bg: #eee !default;\n\n$main-box-shadow: #aaa !default;\n\n$font-color-01:   #fff !default;\n$font-color-02:   #9aaec1 !default;\n$font-color-03:   #54708b !default;\n\n$border-color:    #e9edf0 !default;\n$highlighted:     #e9edf0 !default;\n\n$btn-bg:          #e9edf0 !default;\n$btn-bg-hover:    #d5dadd !default;\n\n$btn-bg2:         #9aaec1 !default;\n$btn-bg2-hover:   #54708b !default;\n\n$theme-gray:      #777 !default;\n$theme-green:     #5cb85c !default;\n$theme-blue:      #5bc0de !default;\n$theme-dark-blue: #337ab7 !default;\n$theme-red:       #d9534f !default;\n$theme-orange:    #f0ad4e !default;\n\n$disabled-background:  rgba(221, 221, 221, 0.3) !default;\n$disabled-color:       #f5f5f5 !default;\n\n$theme-list: (\n  'default': $theme-gray,\n  'green': $theme-green,\n  'blue': $theme-blue,\n  'dark-blue': $theme-dark-blue,\n  'red': $theme-red,\n  'orange': $theme-orange,\n) !default;\n","@import 'variables';\n\n@mixin theming($name, $color) {\n  .theme-#{$name} {\n    .bs-datepicker-head {\n      background-color: $color;\n    }\n\n    .bs-datepicker-body {\n      table {\n        td {\n          span.selected,\n          &.selected span,\n          span[class*=\"select-\"]:after,\n          &[class*=\"select-\"] span:after {\n            background-color: $color;\n          }\n\n          &.week span {\n            color: $color;\n          }\n\n          &.active-week span:hover {\n            cursor: pointer;\n            background-color: $color;\n            color: $font-color-01;\n            opacity: 0.5;\n            transition: 0s;\n          }\n        }\n      }\n    }\n  }\n}\n"]}]);
